/*infographics*/
section.infografias-deslactosados{
    position: relative;
    z-index: 9;
    display: block;
}
section.infografias-deslactosados .header-receta{
    background-color: #0045af;
    width: 100%;
    margin-top: 70px;
}
section.infografias-deslactosados .header-receta .content{
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 10px 20px;
    box-sizing: border-box;
    font-size: 0;
}
section.infografias-deslactosados .header-receta .content .title{
    width: 60%;
    display: inline-block;
    vertical-align: middle;
}
section.infografias-deslactosados .header-receta .content .title h1{
    color: #fff;
    font-size: 38px;
    line-height: 38px;
    margin: 0;
}
section.infografias-deslactosados .header-receta .content .title p#breadcrumbs{
    font-size: 15px;
    color: #fff;
}
section.infografias-deslactosados .header-receta .content .title p#breadcrumbs a{
    color: #fff;
}
section.infografias-deslactosados .header-receta .content .title p.bread{
    font-size: 15px;
    color: #fff;
    font-weight: 600;
}
section.infografias-deslactosados .header-receta .btn-receta{
    width: 40%;
    display: inline-block;
    vertical-align: middle;
    text-align: right;
}
section.infografias-deslactosados .header-receta .btn-receta a{
    font-size: 14px;
    color: #0045af;
    background-color: #ffffff;
    padding: 10px 18px;
    border-radius: 20px;
}
section.infografias-deslactosados .main-infografia{
    width: 100%;
    max-width: 1200px;
    margin: 0 auto 50px auto;
    position: relative;
}
body.no-infografia section.infografias-deslactosados .main-infografia{
    display: none;
}
section.infografias-deslactosados .main-infografia .content img{
    width: 100%;
}
section.infografias-deslactosados article.articulo_completo{
    width: 100%;
    max-width: 1200px;
    margin: 0 auto 30px auto;
    display: none;
}
body.no-infografia section.infografias-deslactosados article.articulo_completo{
    display: block;
    margin-top: 30px;
}
section.infografias-deslactosados article.articulo_completo p#breadcrumbs,
section.infografias-deslactosados article.articulo_completo p#breadcrumbs a{
    color: #646464;
}
section.infografias-deslactosados .main-infografia .panel-layout .custom-html-widget span[typeof="v:Breadcrumb"]{
  display: none;
}
section.infografias-deslactosados .main-infografia .ssbp-wrap{
  display: none;
}
section.infografias-deslactosados .main-infografia .share .ssbp-wrap{
  display: inline-block;
}
section.infografias-deslactosados article .division{
  position: relative;
  margin-bottom: 46px;
}
body.no-infografia section.infografias-deslactosados article .division{
    display: none;
}
section.infografias-deslactosados article .division span.btn{
    width: 60px;
    height: 60px;
    background-size: contain;
    background-image: url('/themes/custom/alqueria/assets/img/infographics/others/arrow.png');
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    margin-left: -30px;
    top: -30px;
    cursor: pointer;
}
section.infografias-deslactosados article .division span.line{
    border: 2px solid #0066B4;
    width: 100%;
    display: block;
    border-style: dashed solid;
}
section.infografias-deslactosados article p.bread{
    font-size: 15px;
    color: #646464;
    font-weight: 600;
}
section.infografias-deslactosados article .content h2{
    font-size: 60px;
    line-height: 52px;
    letter-spacing: -1px;
    margin: 5px 0 15px 0;
    color: #646464;
}
section.infografias-deslactosados article .content img{
    width: 100%;
}
section.infografias-deslactosados article .content .info{
    padding: 30px 0;
    box-sizing: border-box;
}
section.infografias-deslactosados article .content .info p{
    margin-bottom: 10px;
    color: #727272;
}
section.infografias-deslactosados article .content .info ul{
    color: #727272;
}
section.infografias-deslactosados article .content .info p span{
    font-size: 12px;
    vertical-align: super;
}
section.infografias-deslactosados article .content .footer-infografia{
    font-size: 0;
}
section.infografias-deslactosados article .content .footer-infografia .legal{
    width: 60%;
    display: inline-block;
    vertical-align: middle;
    font-size: 12px;
    color: #727272;
}
section.infografias-deslactosados article .content .footer-infografia .share{
    width: 40%;
    display: inline-block;
    vertical-align: middle;
    font-size: 16px;
}
section.infografias-deslactosados .share{
    text-align: right;
    position: relative;
    width: 100%;
    margin-top: 40px;
    padding: 0 20px 20px 0;
    overflow: auto;
    display: -webkit-box;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
            flex-direction: column;
    -webkit-box-align: center;
            align-items: center;
}
section.infografias-deslactosados .share p{
    display: inline-block;
    vertical-align: middle;
    font-weight: 600;
    font-size: 14px;
    margin: 10px;
}
section.infografias-deslactosados .share .ssbp-wrap{
    display: inline-block;
    vertical-align: middle;
}
section.infografias-deslactosados .share .btn{
    width: 400px;
    display: inline-block;
    background-color: #d30a00;
    color: #fff;
    text-align: center;
    padding: 8px 13px;
    border-radius: 20px;
    font-size: 1.5rem;
    cursor: pointer;
    margin-right: 13px;
    font-weight: bold;
}
section.infografias-deslactosados .share .ssbp-wrap li.ssbp-li--facebook{
    background-image: url('/themes/custom/alqueria/assets/img/infographics/others/facebook.png');
    background-size: contain;
    background-repeat: no-repeat;
    width: 120px;
}
section.infografias-deslactosados .share .ssbp-wrap li.ssbp-li--twitter{
    background-image: url('/themes/custom/alqueria/assets/img/infographics/others/twitter.png');
    background-size: contain;
    background-repeat: no-repeat;
    width: 114px;
    margin-left: -3px;
}
section.infografias-deslactosados .share .ssbp-wrap li.ssbp-li--whatsapp{
    background-image: url('/themes/custom/alqueria/assets/img/infographics/others/whatsapp.png');
    background-size: contain;
    background-repeat: no-repeat;
    width: 119px;
    margin-left: 2px;
}
section.infografias-deslactosados .share .ssbp-wrap li.ssbp-li--google,
section.infografias-deslactosados .share .ssbp-wrap li.ssbp-li--linkedin{
    display: none;
}
section.infografias-deslactosados .share .ssbp-wrap li.ssbp-li--facebook a.ssbp-facebook,
section.infografias-deslactosados .share .ssbp-wrap li.ssbp-li--twitter a.ssbp-twitter,
section.infografias-deslactosados .share .ssbp-wrap li.ssbp-li--whatsapp a.ssbp-whatsapp{
    opacity: 0;
    width: 100%;
    height: 41px;
}
section.infografias-deslactosados .slider-recetas-home-deslactosado{
    width: 100%;
    max-width: 1200px;
    margin: 30px auto 50px auto;
    z-index: 1;
    position: relative;
}
section.infografias-deslactosados .slider-recetas-home-deslactosado .owl-nav{
    width: 1200px;
    left: 50%;
    margin-left: -600px;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    z-index: 2;
}
section.infografias-deslactosados .slider-recetas-home-deslactosado .owl-nav .owl-prev{
    position: absolute;
    left: -30px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
