    height: 60px;
    margin: 0;
    background-image: url('/themes/custom/alqueria/assets/img/infographics/others/left.png');
    background-size: contain;
    font-size: 0;
    background-color: transparent;
}
section.infografias-deslactosados .slider-recetas-home-deslactosado .owl-nav .owl-next{
    position: absolute;
    right: -30px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
