    height: 60px;
    margin: 0;
    background-image: url('/themes/custom/alqueria/assets/img/infographics/others/right.png');
    background-size: contain;
    font-size: 0;
    background-color: transparent;
}
section.infografias-deslactosados .slider-recetas-home-deslactosado article{
