  top: 50%;
  left: 50%;
  margin-left: -11%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background-image: url('/themes/custom/alqueria/assets/img/infographics/others/slider-ico-infografia-4.png');
