}
section.infografias-deslactosados .slider-recetas-home-deslactosado article.star .img:after{
  content: "";
  width: 22%;
  height: 38%;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -11%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background-image: url('/themes/custom/alqueria/assets/img/infographics/others/slider-ico-infografia-3.png');
