}
section.infografias-deslactosados .slider-recetas-home-deslactosado article .img:before{
    width: 100%;
    height: 100%;
    content: "";
    display: block;
    position: absolute;
    background-color: rgba(0, 0, 0, 0);
    z-index: 1;
    transition: all .5s;
    -webkit-transition: all .5s;
}
section.infografias-deslactosados .slider-recetas-home-deslactosado article:hover .img:before{
    background-color: rgba(0, 0, 0, .15);
}
