.infografia-header{
    position: relative;
    overflow: hidden;
}
.infografia-header .infografia-box{
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
            align-items: center;
    position: absolute;
    top: 0;
    left: 0;
}
.infografia-header .infografia-box--item:first-child{
    min-width: 50%;
    -webkit-box-ordinal-group:2;
            order:1;
}
@media (max-width:768px){
    .infografia-box .infografia-box--item:first-child{
        padding-right: 6%;
    }
}
@media (max-width: 992px){
    .infografia-box .infografia-box--item:first-child{
        min-width: 50%;
    }
}
@media (min-width: 992px){
    .infografia-header .infografia-box--item:first-child > img{
        max-width: 59%;
        margin-left: 9%;
        margin-bottom: 10px;
    }
}
.infografia-header .infografia-box--item:first-child > img:last-child{
    padding: 0 30px;
}
.infografia-header .infografia-box--item:last-child{
    -webkit-box-ordinal-group: 1;
