        -webkit-transform: rotate(2deg);
                transform: rotate(2deg);
    }
}
.infografia-original{
    position: relative;
    overflow: hidden;
}
.infografia-original .layout-two::before{
    content: '';
    position: absolute;
    top: 68px;
    left: 0;
    background: url('/themes/custom/alqueria/assets/img/infographics/infographic-1/bubble.png') center 40px/100% no-repeat;
    z-index: 1;
    width: 100%;
    height: 100%;
        -webkit-transition: all 2s cubic-bezier(0.21, 1, 0.84, 1.01);
    transition: all 2s cubic-bezier(0.21, 1, 0.84, 1.01);
}
@media (min-width: 992px){
    .infografia-original .layout-two::before{
        top: 76px;
        left: 2px;
    }
}
.infografia-original .layout-two:hover:before{
-webkit-transform: scale(1.2) translate(-7px, 13px);
    transform: scale(1.2) translate(-7px, 13px);
}
.infografia-original .layout-columns-box::before{
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    background: url('/themes/custom/alqueria/assets/img/infographics/infographic-1/original-fondo.png') center/cover no-repeat fixed;
    z-index: 0;
}
.infografia-original .layout-columns-header{
    padding-top: 25px;
    padding-bottom: 25%;
}
@media (min-width: 992px){
    .infografia-original .layout-columns-header{
        padding-bottom: 10%;
    }
}
.infografia-original .layout-columns-header::before{
    content: '';
    display: block;
    background: url('/themes/custom/alqueria/assets/img/infographics/infographic-1/infografia-original-deck.png') center bottom/100% no-repeat;
    width: 100%;
    height: 100%;
    position: absolute;
    bottom: -1px;
    z-index: 0;
}
@media (min-width: 992px){
