/*slider-pricing1*/
.slider-pricing1{
	.content{
		.c-slider-pricing1{
			.s-slider-pricing1{
				padding: 0 7%;
				@media (max-width: $desktop_1024){
					padding: 0;
				}
				.owl-stage{
					padding: 10px 0;
					.owl-item{
						transform: scale(.9);
						&.center{
							transform: scale(1.05);
						}
						.item{
							margin: 5px;
							padding: 40px 30px;
							border-radius: 15px;
							background-color: $blue1;
							box-shadow: 0px 0 10px 0px $shadow;
							.info{
								text-align: center;
								p{
									padding: 15px 0;
								}
								table{
									margin: 0;
									tr{
										background-color: transparent;
										td{
											border: none;
											text-align: center;
											vertical-align: middle;
											border-bottom: 1px solid $gray6;
											span{
												display: block;
												position: relative;

												color: $gray1;
												font-size: 2.45rem;
												font-weight: 700;
												font-style: normal;
												line-height: normal;
												font-family: $Poppins;
												@media (max-width: $desktop_1640){
													font-size: 2.15rem;
												}
											}
										}
										&:last-child{
											td{
												border-bottom: none;
											}
										}
									}
								}
								.button{
									margin-top: 25px;
								}
							}
						}
					}
				}
			}
		}
	}
}
